<template>
  <div>
    <v-row class="father">
    <v-col cols="3" class="drawer">
      <div class="head">
      <v-avatar size="130"  >
      <img src="/TOUXIANG.jpg" />
    </v-avatar>
    <div><h2>倦鸟叫了一整天</h2></div>
    <div><a href="https://beian.miit.gov.cn/" target="_blank">桂ICP备2023004393号</a></div>
  </div>
    </v-col>
    <v-col cols="9">
    <div v-for="item in items" :key="item.link">
      <v-card  elevation="12"  outlined  tile class="box" @click="open(item.link)">
        <v-card-title>{{ item.title }}</v-card-title>
        <v-card-subtitle>{{ item.pubDate }}</v-card-subtitle>
        <v-card-text>{{ item.description }}</v-card-text>
      </v-card>
    </div>
  </v-col>
</v-row>
  </div>
</template>

<script>
import axios from 'axios';
import X2JS from "x2js";
const x2js = new X2JS();
export default {
  data () {
    return {
      content:'',
      items:[],
    }
  },
  methods:{
    open(url)
    {
      window.open(url,"_blank")
    }
  },
  mounted()
  {
    let xhr = null
    if (window.XMLHttpRequest) {
      xhr = new XMLHttpRequest()
    } else {
      // eslint-disable-next-line
      xhr = new ActiveXObject('Microsoft.XMLHTTP')
    }
    //本地文件路径前缀不要加public，否则找不到
    const okStatus = document.location.protocol === 'file' ? 0 : 200
    xhr.open('GET', '/RSS.xml', false)
    xhr.overrideMimeType('text/html;charset=utf-8')
    xhr.send(null)
    this.content= xhr.status === okStatus ? xhr.responseText : null
    // axios.get('/RSS.xml').then(
    //   res=>{
    //     this.content = res.data;
    //     console.log(this.content);
    //   }
    // )
    const xmlContent = x2js.xml2js(this.content);
    this.items=xmlContent.rss.channel.item;
    console.log(this.items);
}
}
</script>

<style  scoped>
.drawer{
  display: flex;
  justify-content: center;
}
.head{
  position:fixed;
  top:250px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.box{
  width:60%;
  margin-top:20px;
  margin-bottom: 10px;
}
a{
  color: black;
  text-decoration:none;
  }
.drawer div{
  margin-bottom:20px;
}
</style>
